#contact-page-container {
    display: grid;
    grid-template-areas:
        'contact-form'
        'info-box';
}

#contact-form {
    grid-area: contact-form;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10%;
}

#info-box {
    grid-area: info-box;
    margin-top: 10%;
    margin-bottom: 15%;
    display: flex;
    align-items: center;
}

@media (min-width: 767px) {
    #contact-page-container {
        grid-template-columns: 1fr 1fr;
        grid-template-areas: 'contact-form info-box';
    }
    #contact-form {
        margin-bottom: 15%;
    }
}

#contact-form-wrapper {
    width: 60%;
}

#contact-form-container {
    background: linear-gradient(
        rgba(255, 255, 255, 0.15),
        rgba(255, 255, 255, 0.2),
        rgba(255, 255, 255, 0.15)
    );
    -webkit-backdrop-filter: blur(6px);
            backdrop-filter: blur(6px);
    border: 0.2em solid #90e0ef;
    border-radius: 7px;
    padding: 9% 7% 0% 7%;
}

#main-contact-form {
    margin: 12% auto;
}

