}

.contact-form-fields::-webkit-input-placeholder {
    -webkit-text-stroke: 1px rgba(0, 255, 255, 0.75);
    font-size: 1.5rem;
    outline: 0px !important;
    -webkit-appearance: none;
    box-shadow: none !important;
    background: none;
    text-decoration: none;
    color: #90e0ef;
}

.contact-form-fields:-ms-input-placeholder {
    -webkit-text-stroke: 1px rgba(0, 255, 255, 0.75);
    font-size: 1.5rem;
    outline: 0px !important;
    -webkit-appearance: none;
    box-shadow: none !important;
    background: none;
    text-decoration: none;
    color: #90e0ef;
}

.contact-form-fields,
.contact-form-fields:hover,
.contact-form-fields:focus,
.contact-form-fields:active,
.contact-form-fields::placeholder {
    -webkit-text-stroke: 1px rgba(0, 255, 255, 0.75);
    font-size: 1.5rem;
    outline: 0px !important;
    -webkit-appearance: none;
    box-shadow: none !important;
    background: none;
    text-decoration: none;
    color: #90e0ef;
}

